
.PhotoDividerComponent {
  min-width: 100vw;
  margin: 100px 0;
  height: 300px;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 568px) {
  .PhotoDividerComponent {
    min-width: 100vw;
    margin: 50px 0;
    height: 200px;

  }
}