.card {
    display: inline-block;
    max-width: 350px;
    margin: 30px 15px;
    line-height: 1.42857143;
    border-radius: .2em;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    transition: box-shadow .25s;
}

.card:hover {
    box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card .card-image {
    width: 100%;
    height: 250px;
    border-top-left-radius: .2em;
    border-top-right-radius: .2em;
    display: block;
    overflow: hidden;
}

.card .card-image img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    transition: all .25s ease;
    object-position: 50% 5%;
}

.card .content {
    padding: 5px 15px 15px 15px;
    text-align: left;
}

.card .content .likes {
    float: right;
}

.card .content .likes i {
    color: #f2bd54;
    padding-right: 5px;
}

.card .content .likes p {
    color: black;
    display: inline-block;
    margin: 5px 0 0 0;
}

.card .content h4 {
    font-size: 25px;
    margin: 20px 0 5px 0;
}

.card .content h5 {
    font-size: 17px;
    margin: 0 0 4px 0;
}

.card .content p {
    font-size: 15px;
}

.card .title {
    margin-top: 0px;
    font-weight: 700;
    font-size: 1.65em;
}

.card .title a {
    color: #000;
    text-decoration: none;
}

.card .read-more {
    border-top: 1px solid #d4d4d4;
    background-color: #ffffff11;
    border-bottom-left-radius: .2em;
    border-bottom-right-radius: .2em;
    transition: .5s;
}

.card .read-more span {
    text-decoration: none;
    display: block;
    margin: 0 auto;
    text-align: center;
    font-size: 18px;
    background-color: #147185;
    color: white;
    padding: 15px;
    border-bottom-left-radius: .2em;
    border-bottom-right-radius: .2em;
    transition: .3s;
    cursor: pointer;
}

.card .read-more span:hover {
    background-color: white;
    color: #213459;
}

.chevron-right {
    vertical-align: middle;
}

@media screen and (max-height: 568px) {
    .card .card-image {
        height: 150px;
    }

    .card .content h4 {
        font-size: 19px;
        margin: 5px 0 5px 0;
    }

    .card .content {
        height: auto;
    }

    .card .content p {
        font-size: 15px;
        margin: 0;
    }
}

@media screen and (max-width: 1000px) {
    .card {width: 250px;}
}

@media screen and (max-width: 414px) {
    .card {width: 400px;}
}


@media (prefers-color-scheme: dark) {
    .card {
        background-color:#333;
        box-shadow: 0 2px 5px 0 rgba(255, 255, 255, 0.16), 0 2px 10px 0 rgba(255, 255, 255, 0.12);
    }

    .card:hover {
        box-shadow: 0 8px 17px 0 rgba(255, 255, 255, 0.2), 0 6px 20px 0 rgba(255, 255, 255, 0.19);
    }
    
}